.app{height:100vh}.body{display:flex;background-color:#B3B3B3}body{margin:0}p,li{font-family:"PT Sans Caption",sans-serif;font-size:1em}button,a{font-family:"Puritan",sans-serif;font-size:1.2em}h1{font-family:"Raleway",sans-serif;font-size:2.5em}h2{font-family:"Puritan",sans-serif;font-size:1.7em}h3{font-family:"Puritan",sans-serif;font-size:1.5em}

.main-nav{background-color:#1D4938;color:#B3B3B3}.site-header{text-align:center;color:white}.logo{width:30vw;margin:15px 15px -10px 15px}

.nav-search{display:flex;justify-content:center;color:#1D4938}.search-bar{height:30px;padding:2.5% 4%;width:70%;margin:5% 0 5% 4px;outline:none;box-shadow:inset 0px 0px 5px 0px black}

.park-list{display:flex;flex-direction:column;align-items:center;overflow-y:auto;direction:rtl;margin-top:20px;height:100vh;position:relative}.randomize-button{cursor:pointer;width:80%;margin-bottom:4%;background-color:#B3B3B3;box-shadow:5px 5px 5px black}.randomize-button:hover{transform:scale(1.05);transition-duration:.4s}.randomize-icon{width:20%}

.park-button{padding:5% 0;width:100%;cursor:pointer;background-color:#B3B3B3;box-shadow:5px 5px 5px black}.park-button:hover{transform:scale(1.05);transition-duration:.4s}.main-nav-link{width:80%;margin:4%}

.dropdown-form{display:flex;flex-direction:column;align-items:center;color:#EDEDED;font-family:"PT Sans Caption",sans-serif}fieldset{display:flex;flex-direction:column;align-items:center;width:100%;border:none;padding:0}select{height:50px;padding:0 4%;width:80%;margin:5% 0 5% 4px;outline:none;color:gray;box-shadow:inset 0px 0px 5px 0px black}

.banner{width:100%}.header-container{text-align:center;padding:2%;color:black;box-shadow:0px 6px 11px black}.header{font-size:36px;padding:1%;margin:0;font-family:"Raleway",sans-serif}.caption{font-family:"PT Sans Caption",sans-serif}.image-container{background-color:rgba(33,74,63,0.45);height:70vh;display:flex;justify-content:center;align-items:center;box-shadow:inset 0px -9px 7px black}.banner-img{height:80%;max-width:90%;border:white 10px solid;box-shadow:5px 5px 5px black}.banner-btn-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;align-items:center}.saved-icon{height:50px}.banner-icons{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}

.user-nav{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;justify-content:space-around;background-color:#514742;color:white;width:100%;z-index:1;box-shadow:0px 6px 11px black}.user-nav h2{color:white;-webkit-text-decoration:white underline solid;text-decoration:white underline solid}.user-nav h2:hover{color:#70977E;-webkit-text-decoration:#70977E underline solid;text-decoration:#70977E underline solid}

.park-info{background-color:#EDEDED;padding:1em}

ul{-moz-column-count:3;-moz-column-gap:20px;-webkit-column-count:3;-webkit-column-gap:20px;column-count:3;grid-column-gap:20px;column-gap:20px}

.map-container{display:flex;justify-content:center}.map{width:60vw;height:300px;box-shadow:black 5px 5px 5px}

.contact-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;justify-content:space-around}

.banner-icons{display:flex;flex-direction:row;justify-content:space-around;z-index:1;margin-top:3%}.banner-btn-container:hover{transform:scale(1.1);transition-duration:0.4s;cursor:pointer}

.saved-park-img{width:100%}.saved-park-card{display:flex;flex-direction:column;align-items:center;width:40%;margin:3%;background-color:#EDEDED;padding:2%;border-radius:5px;box-shadow:5px 5px 5px black}.saved-park-card a{color:#1D4938}.saved-park-card button{background-color:#514742;padding:3%;border-radius:5px;color:white;cursor:pointer;margin:2%;width:250px}.saved-park-card button:hover{transform:scale(1.05);transition-duration:0.4s}

.saved-parks-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}

.park-container{width:70vw;overflow-x:hidden}

.footer-btn-container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;height:15vh;background-color:#514742;position:relative;align-items:center;justify-content:space-around;box-shadow:0px -5px 5px black}.footer-btn{cursor:pointer;width:300px;font-size:18px;border:none;padding:8%;margin:0 5%;text-align:center;background-color:#B3B3B3;box-shadow:5px 5px 5px black}.footer-btn:hover{transform:scale(1.1);transition-duration:.4s}

